.radio {
    position: absolute;
    left: 310px;
    top: 160px;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    list-style-type: none;
    padding: 0;

    li {
        margin: 0 10px 0 50px;
        z-index: 1;
    }
}

#cancel {
    width: 225px;
    padding: 0 40px 0 40px;
    border-radius: 10px;
	border: 1px solid grey;
    margin-left: 30px;
    z-index: 1;
	transition: all 600ms;
	&:hover {
		cursor: pointer;
		letter-spacing: 0.8px;
	}
}